#root .budget-container {
	margin-top: 2.5rem;
	display: flex;
	width: 100vw;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	--tw-bg-opacity: 1;
	background-color: rgba(255, 255, 255, var(--tw-bg-opacity));
	padding-left: 5rem;
	padding-right: 5rem;
	font-family: sans-serif;
}
@media (min-width: 1024px) {

	#root .budget-container {
		flex-direction: row;
	}
}
#root .budget-container-body {
	display: flex;
	width: 100%;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}
#root .budget-container h1 {
	align-self: flex-start;
	--tw-text-opacity: 1;
	color: rgba(50, 63, 82, var(--tw-text-opacity));
}
#root .budgetcard {
	margin-left: 2.5rem;
	margin-right: 2.5rem;
	width: 100%;
}
#root .budget {
	margin-top: 2.5rem;
	margin-bottom: 2.5rem;
}
#root .switch-mode {
	margin-top: 0.75rem;
	margin-bottom: 0.75rem;
	display: flex;
	width: 100%;
	justify-content: center;
}
#root .switch-mode > :not([hidden]) ~ :not([hidden]) {
	--tw-space-x-reverse: 0;
	margin-right: calc(1.5rem * var(--tw-space-x-reverse));
	margin-left: calc(1.5rem * calc(1 - var(--tw-space-x-reverse)));
}
#root .switch-mode > button, .continue {
	width: 50%;
	cursor: pointer;
	border-radius: 9999px;
	border-width: 1px;
	--tw-border-opacity: 1;
	border-color: rgba(62, 185, 136, var(--tw-border-opacity));
	--tw-bg-opacity: 1;
	background-color: rgba(62, 185, 136, var(--tw-bg-opacity));
	padding: 0.5rem;
	--tw-text-opacity: 1;
	color: rgba(255, 255, 255, var(--tw-text-opacity));
}
#root .switch-mode > button:hover, .continue:hover {
	--tw-bg-opacity: 1;
	background-color: rgba(52, 156, 115, var(--tw-bg-opacity));
}
#root .switch-mode > button.active {
	width: 50%;
	border-radius: 9999px;
	border-width: 1px;
	--tw-border-opacity: 1;
	border-color: rgba(52, 156, 115, var(--tw-border-opacity));
	--tw-bg-opacity: 1;
	background-color: rgba(52, 156, 115, var(--tw-bg-opacity));
	--tw-text-opacity: 1;
	color: rgba(255, 255, 255, var(--tw-text-opacity));
}
#root .budget-container-body form {
	display: flex;
	width: 100%;
	flex-direction: column;
}
#root .budget-container-body form > :not([hidden]) ~ :not([hidden]) {
	--tw-space-y-reverse: 0;
	margin-top: calc(1rem * calc(1 - var(--tw-space-y-reverse)));
	margin-bottom: calc(1rem * var(--tw-space-y-reverse));
}
#root .budget-container input, .budget-container-body textarea {
	width: 100%;
	border-radius: 0.375rem;
	border-width: 2px;
	--tw-border-opacity: 1;
	border-color: rgba(229, 231, 235, var(--tw-border-opacity));
	padding-top: 0.5rem;
	padding-bottom: 0.5rem;
	padding-left: 1rem;
	padding-right: 1rem;
}
#root .continue {
	width: 100%;
}
#root .contact-image {
	border-radius: 0.5rem;
	--tw-drop-shadow: drop-shadow(0 25px 25px rgba(0, 0, 0, 0.15));
	-webkit-filter: var(--tw-filter);
	        filter: var(--tw-filter);
	filter: var(--tw-filter);
}
.my-8 {
	margin-top: 2rem;
	margin-bottom: 2rem;
}
.my-3 {
	margin-top: 0.75rem;
	margin-bottom: 0.75rem;
}
.mb-4 {
	margin-bottom: 1rem;
}
.flex {
	display: flex;
}
.hidden {
	display: none;
}
.w-full {
	width: 100%;
}
.w-2\/3 {
	width: 66.666667%;
}
.w-1\/3 {
	width: 33.333333%;
}
.w-1\/2 {
	width: 50%;
}
.flex-row {
	flex-direction: row;
}
.justify-center {
	justify-content: center;
}
.rounded-md {
	border-radius: 0.375rem;
}
.text-5xl {
	font-size: 3rem;
	line-height: 1;
}
.text-2xl {
	font-size: 1.5rem;
	line-height: 2rem;
}
.text-gray-500 {
	--tw-text-opacity: 1;
	color: rgba(107, 114, 128, var(--tw-text-opacity));
}
