@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
    #root .budget-container {
       @apply flex flex-col lg:flex-row justify-center w-screen items-center bg-white px-20 font-sans-serif mt-10
    }
    #root .budget-container-body {
        @apply flex flex-col items-center justify-center w-full
    }
    #root .budget-container h1 {
        @apply text-primary self-start
    }
    #root .budgetcard {
        @apply w-full mx-10 
    }
    #root .budget {
        @apply my-10
    }
    #root .switch-mode {
        @apply flex justify-center my-3 w-full space-x-6
    }
    #root .switch-mode > button, .continue {
        @apply w-1/2 p-2 border border-secondary-light cursor-pointer bg-secondary-light text-primary-light hover:bg-secondary-dark rounded-full
    }
    #root .switch-mode > button.active {
        @apply w-1/2 bg-secondary-dark border border-secondary-dark text-primary-light rounded-full
    }
    #root .budget-container-body form {
        @apply flex flex-col w-full space-y-4
    }
   
    #root .budget-container input, .budget-container-body textarea {
        @apply py-2 px-4 border-2 border-gray-200 w-full rounded-md
    }
    #root .continue {
        @apply w-full
    }
    #root .contact-image {
        @apply rounded-lg filter drop-shadow-2xl
    }
}


